@Composable
fun Rolunk(ratios: ratios){

Card(
    modifier = Modifier.fillMaxWidth(ratios.horizontal)

){
if(ratios.elrendezes== elrendezes.Horizontal)   Row(verticalAlignment = Alignment.CenterVertically, modifier = Modifier.padding(ratios.smallpadding)) {
    ContentMelinda(ratios)
   }
    else Column(horizontalAlignment = Alignment.CenterHorizontally, modifier = Modifier.padding(ratios.smallpadding)) {
        ContentMelinda(ratios)
    }
    Spacer(ratios.space)
    if(ratios.elrendezes== elrendezes.Horizontal)   Row(verticalAlignment = Alignment.CenterVertically, modifier = Modifier.padding(ratios.smallpadding)) {
        ContentJnos(ratios)
    }
    else Column(horizontalAlignment = Alignment.CenterHorizontally, modifier = Modifier.padding(ratios.smallpadding)) {
        ContentJnos(ratios)
    }
}
}

@Composable
fun ContentMelinda(ratios:ratios){
    Column(
        horizontalAlignment = Alignment.CenterHorizontally,
        modifier = Modifier.padding(8.dp)
    ) {
        Image(painter = painterResource(Res.drawable.Melinda_circle),modifier= Modifier.clip(
            RoundedCornerShape(16.dp)
        ).size(200.dp),contentDescription = "Csti Melinda")
        Text("Csti Melinda")
        Text("Matematikatanr", textAlign = TextAlign.Center)
    }
    OutlinedCard(
        Modifier.padding(ratios.padding)
    ) {
        Column(
            modifier=Modifier.wrapContentHeight().fillMaxWidth().padding(ratios.padding)
        ) {
            Text(
                stringResource(Res.string.Melinda_CV_I), modifier = Modifier.fillMaxWidth(),
                textAlign = TextAlign.Justify
            )
        }
    }
}

@Composable
fun ContentJnos(ratios:ratios){
    Column(
        horizontalAlignment = Alignment.CenterHorizontally,
        modifier = Modifier.padding(8.dp)
    ) {
        Image(painter = painterResource(Res.drawable.KovacsJanos_circle),modifier= Modifier.clip(
            RoundedCornerShape(16.dp)
        ).size(200.dp),contentDescription = "Kovcs Jnos")
        Text("Kovcs Jnos")
        Text("Magyar Fiatal Tudsok Trsasgnak tagja", textAlign = TextAlign.Center)
        Text("Szegedi Tudomnyegyetem\nMolekulris Bionika Mrnk hallgatja", textAlign = TextAlign.Center)
    }
    OutlinedCard(
        Modifier.padding(ratios.padding)
    ) {
        Column(
            modifier=Modifier.wrapContentHeight().fillMaxWidth().padding(ratios.padding)
        ) {
            Text(
                "Hatodik osztlyos dikknt kezdtem programozst tanulni, mikor a Dr. Code programoziskola els kezd kurzusait elindtotta ltalnos iskolmban, s addigi tanulmnyi eredmnyeimnek ksznheten felajnlottak nekem egy sztndjas helyet. A kurzusokon elszr Scratch-ben sajttottuk el az algoritmikus gondolkozs alapjait, majd MIT App Inventorban tanultunk Android alkalmazs fejlesztst. Vgl egy webfejleszt kurzuson a HTML s JavaScript alapjait ismertem meg. Ezek az ismeretek adtk az eszkztramat, (melyek mindegyikt tudtam hasznostani idkzben) mikor 2019-ben elkezdtnk dolgozni a MateKIT-en. Clknt azt fogalmaztuk meg, hogy az iskolkban akkor jonnan rendelkezsre ll okostblk s tblagpek felhasznlsval vlaszoljunk a differencilt oktats kihvsaira. A MateKIT fejlesztst MIT App Inventorban kezdtem, viszont hamar szembesltem a platform korltaival. Nmi utnajrs utn az j Kotlin programozsi nyelvre esett a vlaszts, mely abban az vben lett a Google ltal hivatalosan ajnlott Android programozsi nyelv. gy a MateKIT fejlesztshez azta mindig a legmodernebb keretrendszert alkalmazzuk, kvetve a technolgia fejldst.", modifier = Modifier.fillMaxWidth(),
                textAlign = TextAlign.Justify
            )
        }
    }
}